import { h, render } from "preact";
import App from "./src/containers/App/app";
import "./styles.scss";

const fakeData = {
  "instagram-feed-11": {
    buttonID: "id1",
    instagramID: "",
  },
  "instagram-feed-12": {
    buttonID: "id2",
    instagramID: "",
  },
};

(window as any).instafeedHubElements = fakeData;

const instafeedHubElements: Record<
  string,
  {
    buttonID: string;
    instagramID: string;
  }
> = (window as any).instafeedHubElements;

const elements = Object.values(instafeedHubElements).map(
  (item) => item.buttonID
);

elements.forEach((id) => {
  const item = document.querySelector(`#${id}`);
  item.addEventListener("click", () => {
    item.classList.add("isClicked");
    console.log(999);
    render(<App clientId="clientId1" instaId="instaId1" />, item);
  });
});

